.header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	height: 5em;
	background-color: #003049;
	color: #eae2b7;
	width: 100%;
}

.title {
	padding-left: 1.2em;
	padding-right: 1.2em;
}

.navigation {
	display: flex;
	gap: 5em;
	margin-left: auto;
	padding-right: 5em;
	color: #eae2b7;
	font-size: 1.2em;
}

.navButton {
	display: flex;
	flex-grow: 1;
	justify-content: flex-end;
	color: #fcbf49;
	text-decoration: none;
	transition: color 0.5s;
	text-align: center;
	align-items: center;
}

.navButton:hover {
	color: #f77f00;
}

.checkoutButton {
	flex-grow: 0;
	color: #d62828;
	background-color: #003049;
	border: none;
	font-size: 1.5em;
	padding-right: 0.2em;
}

.checkoutSection {
	display: flex;
	align-items: flex-start;
	justify-content: flex-end;
	padding-right: 2.2em;
	text-decoration: none;
	color: #eae2b7;
}

.itemsCounter {
	width: 1em;
	font-size: 1em;
	font-weight: 300;
}

.user {
	display: flex;
	align-items: center;
	justify-content: center;
	margin-right: 1em;
}

.displayName {
	white-space: nowrap;
	margin-right: 0.1em;
}

.userPhoto {
	width: 2em;
	border-radius: 50%;
	pointer-events: none;
}

.signOut {
	background: none;
	border: none;
}

.hidden {
	opacity: 0;
	transform: translate(0, 1em);
	transition: 0.5s;
	background-color: #f77f00;
	border-radius: 20px;
	border: none;
	margin-left: 25%;
}

.visible {
	opacity: 1;
	transform: translate(0, 2em);
}

.signOutButn {
	background-color: #fcbf49;
	padding: 2em;
}

.signOutMenu {
	display: flex;
	flex-direction: column;
	align-items: flex-end;
}

.logSection {
	position: relative;
	top: 14%;
}
